import firebase from 'firebase'
import { signOut } from './auth'
import { cleanProgress } from './upload_progress'


const footer = () => {
  const d = document,
    c = console.log
  /*Todo el codigo de programacion asincrono y que interactue con
  codigo html dinamico cono el retur , va a cargar
  10 milisegundos despues de que carge el DOM*/
  const footerScripts = setInterval(() => {
    if (d.readyState === 'complete') {
      //Para limpiar
      clearInterval(footerScripts)
      /*Selecciona los elementos del menu de navegacion
            Guarda las secciones*/
      const nav = d.querySelector('.Footer-menu'),
        sections = d.querySelectorAll('.Content-section')

      nav.addEventListener('click', e => {
        e.preventDefault()
        //T llea al top de la pagina
        window.scroll(0,0)
        /*SI el evento padre que origino el evento en este caso Footer coincide con que
        sea un button*/

        if (e.target.parentElement.matches('button')) {
          //Para saber cual es el boton que estamos presionando
          //className devuelve el atributo que esta solicitando ,el string completo
          let btn = e.target.parentElement,
            btnSection = btn.className.split('-')[0]

          c(btn, btnSection)
          /*Por cada seccion*/
          sections.forEach(section => {
            if (section.classList.contains(btnSection)) {
              //XK kiero k se muestre
              section.classList.add('u-show' , 'u-fadein')
              section.classList.remove('u-hide')
            } else {
              section.classList.add('u-hide')
              section.classList.remove('u-show' , 'u-fadein')
            }
          })
          cleanProgress()

        }
      })
    }
  }, 100)

  return `
    <footer class="Footer  u-fixed">
      <nav class="Footer-menu">
        <button class="Profile-button" title="Perfil">
          <i class="fa fa-user"></i>
        </button>
        <button class="Uploader-button" title="Subir Foto">
          <i class="fa fa-picture-o"></i>
        </button>
        <button class="Timeline-button" title="Home">
          <i class="fa fa-home"></i>
        </button>
        <button class="Camera-button" title="Cmara">
          <i class="fa fa-camera"></i>
        </button>
        ${signOut()}
      </nav>
    </footer>
  `
}

export default footer



