import firebase from 'firebase'
import { progressBar, progressStatus, showProgress, hideProgress } from './upload_progress'
import { errorMsg, successMsg } from './helpers/messages'
import { savePhotoInDB } from './helpers/photos_db'

const camera = () => {
const d = document,
n = navigator,
c = console.log
/*Todo el codigo de programacion asincrono y que interactue con
codigo html dinamico cono el retur , va a cargar
10 milisegundos despues de que carge el DOM*/

const cameraScripts= setInterval(()=>{
  if(d.readyState ==='complete'){
    //Para limpiar
    clearInterval(cameraScripts)
    const cameraApp = d.querySelector('.Camera'),
        video = d.getElementById('camera-stream'),
        photo = d.getElementById('photo'),
        startCameraBtn = d.getElementById('start-camera'),
        output = d.querySelector('.Camera').querySelector('.Progress-output'),
        controls= d.querySelector('.Camera-menu'),
        //takePhotoBtn toma la foto
        takePhotoBtn = d.getElementById('take-photo'),
        deletePhotoBtn = d.getElementById('delete-photo'),
        uploadPhotoBtn = d.getElementById('upload-photo'),
        downloadPhotoBtn = d.getElementById('download-photo'),
        canvas = d.getElementById('canvas-snap'),
        context = canvas.getContext('2d')

        //cuando yo tome de nuevo la foto es un nuevo bloque de datos
        let snapshot

      function cameraInit() {
        n.getMedia = (
          n.getUserMedia ||
          n.webkitGetUserMedia ||
          n.mozGetUserMedia ||
          n.msGetUserMedia
        )

        if (!n.getMedia) {
          output.innerHTML = errorMsg('Tu navegador NO soporta el uso de la cmara de tu dispositivo', null)
        } else {
          //recibe 3 parametros
          //un objeto de los elementos que voy a utilizar
          //una funcion para poder activar la camara
          //manejar un error en caso de que la camara no se ative
          n.getMedia(
            { video: true },
            stream => {
              //video.src = window.URL.createObjectURL(stream)
              //Este error se debe a que la funcin createObjectURLest obsoleta para Google Chrome.
              //https://stackoverflow.com/questions/27120757/failed-to-execute-createobjecturl-on-url
              video.srcObject=stream
              video.play()
            },
            err => output.innerHTML = errorMsg(`Hubo un error al acceder a la cmara de tu dispositivo: ${err.mesagge}`, err)
          )
        }
      }

      function takeSnapshot(){
        //ancho del videov
        let width= video.videoWidth,
        height = video.videoHeight
        //el canvas sirve para generar la imagen
        canvas.width=width
        canvas.height=height
        //ppoder dibujar una imagen
        context.drawImage(video,10,10)

        return canvas.toDataURL('image/png')
      }

      function cameraRest(){
        //Volver a mostrar y reproducir el video
        video.style.display='block'
        video.play()
        //Limpiar el snapshot de la imagen dinamica
        photo.style.display = 'none'
        photo.setAttribute('src','')
        //Limpiar el enlace de descarga
        downloadPhotoBtn.querySelector('a').href = '#'
        //Habilitar botones
        deletePhotoBtn.classList.add('u-disabled')
        uploadPhotoBtn.classList.add('u-disabled')
        downloadPhotoBtn.classList.add('u-disabled')
      }

        cameraInit()
        //para ocultar el canvas
        canvas.style.display = 'none'
        photo.style.display='none'
        //para encender la camara
        startCameraBtn.addEventListener('click', e=>{
          e.preventDefault()
          video.play()
          video.style.display = 'block'
          photo.style.display = 'none'
        })

        takePhotoBtn.addEventListener('click', e=>{
          e.preventDefault()
          //Pausamos y Ocultamos el video
          video.style.display = 'none'
          video.pause()
          //Mostramos la captura de la cmara en una imgen
          snapshot = takeSnapshot()
          photo.style.display = 'block'
          photo.setAttribute('src', snapshot)
          //Asignar la imagen capturada al enlace de descarga
          downloadPhotoBtn.querySelector('a').href = snapshot
          //Habilitar botones de eliminar, subir y descargar
          deletePhotoBtn.classList.remove('u-disabled')
          uploadPhotoBtn.classList.remove('u-disabled')
          downloadPhotoBtn.classList.remove('u-disabled')
        })

        deletePhotoBtn.addEventListener('click', e=>{
          e.preventDefault
          cameraRest()
        })

        uploadPhotoBtn.addEventListener('click', e=>{
          e.preventDefault()
        const storageRef = firebase.storage().ref().child('edgram/photos'),
          dbRef = firebase.database().ref().child('edgram/photos'),
          user = firebase.auth().currentUser
          /*nombre d la imagen que toy subiendo
          subir la imagen putString sube archivos en formato URL
          parametro donde almaceno la iagen dataURL -> snapshot*/
        let photoName = `photo_${Math.floor(Math.random() * 10000000)}`,
          uploadTask = storageRef.child(photoName).putString(snapshot, 'data_url')

          uploadTask.on('state_changed', data => {
            //c(data)
            //Muestra el progreso
            showProgress()
            //Conversion de que esta subiendo la imagen y que la barra va aumentando
            progressStatus(data)
          }, err => {
            //c(err, err.code, err.mesagge)
            output.innerHTML = errorMsg(`${err.mesagge}`, err)
          }, () => {
            storageRef.child(photoName).getDownloadURL()
              .then(url => {
                //c(url)
                output.innerHTML = successMsg('Tu foto se ha subido')
                //Guardar la foto en la BD
                savePhotoInDB(url, user)
                //Ocultar el progreso
                hideProgress()
                //Para que despues de tres segunos borre el contendio del html pa poder tomar otra foto
                setTimeout(() => output.innerHTML = '', 3000)
                //Vuelva a tener acceso al video
                cameraRest()
              })
              .catch(err => output.innerHTML = errorMsg(`${err.mesagge}`, err))
          })

        })

  }
  /*img pra mostrar la captura del video, para generar
  una captura de un video se usa una Api de canvas
  canvas es el source que almacenara <img>*/
  /*download con esta propiedad se puede descargarel contenido que se tenga es decir
  guardar la imagen generar una imagen unica se uso ramdon y
  cada vez que se descarge una imgane sera selfie1234 */
},100)
  return  `
  <article id="camera-app" class="Camera  Content-section  u-hide">
  <video muted id="camera-stream" class="Camera-video" ></video>
  <img id="photo" class="Camera-photo"  >
  <nav class="Camera-menu">
    <button id="start-camera" title="Iniciar Cmara"><i class="fa fa-power-off"></i></button>
    <button id="take-photo" title="Tomar Foto"><i class="fa fa-camera"></i></button>
    <button class="u-disabled" id="delete-photo" title="Borrar Foto"><i class="fa fa-trash"></i></button>
    <button class="u-disabled" id="upload-photo" title="Subir Foto"><i class="fa fa-upload"></i></button>
    <button class="u-disabled" id="download-photo" title="Guardar Foto"><a href="#" download="selfie_${Math.floor(Math.random() * 10000000)}.png"><i class="fa fa-download"></i></a></button>
  </nav>
  <canvas id="canvas-snap" ></canvas>
  ${progressBar()}
</article>
  `
}

export default camera



