import firebase from 'firebase'
import { progressBar, progressStatus, showProgress, hideProgress } from './upload_progress'
import { errorMsg, successMsg } from './helpers/messages'
import { savePhotoInDB } from './helpers/photos_db'

const uploader = () => {
  const d = document,
    c = console.log
  /*Todo el codigo de programacion asincrono y que interactue con
  codigo html dinamico cono el retur , va a cargar
  10 milisegundos despues de que carge el DOM*/

  const uploaderScripts = setInterval(() => {
    if (d.readyState === 'complete') {
      //Para limpiar
      clearInterval(uploaderScripts)

      const storageRef = firebase.storage().ref().child('photos'),
        dbRef = firebase.database().ref().child('photo'),
        //Solicita el servicio de firebase auth, currenUser es el usuario actual
        user = firebase.auth().currentUser,
        //Guardar en una variable el formulario
        form = d.getElementById('upload'),
        uploader = d.getElementById('uploader'),
        output = d.querySelector('.Uploader').querySelector('.Progress-output')

      uploader.addEventListener('change', e => {
        //alert('funciona')
        //Muestra en formato JSON los archivos de las fotos que subo
        //c(e.target.files)
        //Aqui donde imprimire los mensajes o imagenes que se vallan subiendo
        output.innerHTML=''

        Array.from(e.target.files).forEach(file => {
          //c(e.target.files, file)
          //Valida que se suba una imagen con elformato correcto
          if (file.type.match('image.*')) {
          //c('Subimos')
          //output.innerHTML=successMsg('Tu archivo si es una imagen')
          let uploadTask = storageRef.child(file.name).put(file)

          uploadTask.on('state_changed', data =>{
            //Envia datos  c(data)
            showProgress()
            progressStatus(data)

          },err =>{
            //Si desactivo el serviceWorked se desconecta el firebase tabn
            //c(err, err.code,err.message)
            output.innerHTML=errorMsg(`${err.message}%`, err)
          },()=>{
            //visualizar la foto
            //obtiene la URL getDownloadURL y es unapromesa y nos devuelve una url
            storageRef.child(file.name).getDownloadURL()
            .then( url =>{
              c(url)
              //Insertar  codigo html insertAdjacentHTML
              output.insertAdjacentHTML(
                'afterbegin',
                `${successMsg('Tu Foto se ah subido')}
              <img src="${url}">`)
              //user se crear al momento de indicar la autenticacion
              savePhotoInDB(url,user)

              //Limpiar la pantalla cuando se sube una imagen
              hideProgress()

            })
            .catch(err => output.innerHTML=errorMsg(`${err.message}`, err) )
          })
          //output.innerHTML=successMsg('Tu archivo si es una imagen')
        }else{
          //c('No subimos')
          output.innerHTML=errorMsg('Tu archivo debe ser una imagen', null)
        }
      })
      form.reset()
      })
      //c(user)


      /*Otro MODO
      //storage manda a llamar el servicio de lamacenamento de storage
      const storage= firebase.storage(),
      //bucket va a ser la referencia a mi careta virtual de mi servico de firebase
      bucket=storage.ref(),
      //photosRef va a ser la referencia crear un hijo que se llame photos
      photosRef=bucket.child('photo'),
      Referencia al servicio de mi sevicio, luego a la referencia,luego al objeto Json crea unn
      objeto que se llame foto y ahi guarda la descripcion de las photos
      fileRef=firebase.database().ref().child('photo'),
      user= firebase.auth().currentUser
      */

    }
  }, 100)

  return `
  <article class="Uploader  Content-section  u-hide">
  <h2 class="u-title">Sube tus Fotos</h2>
  <form name="upload" id="upload">
    <input type="file" id="uploader" multiple>
    <label for="uploader">
      <i class="fa fa-cloud-upload" title ="Subir Foto(s)"></i>
    </label>
  </form>
  ${progressBar()}
</article>
  `
}

export default uploader



