import firebase from 'firebase'
const profile = () => {
const d = document,
c = console.log,
user = firebase.auth().currentUser,
dbRef=firebase.database().ref().child('photos')
let profilePhotos=''

/*Todo el codigo de programacion asincrono y que interactue con
codigo html dinamico cono el retur , va a cargar
10 milisegundos despues de que carge el DOM*/

const profileScripts= setInterval(()=>{
  if(d.readyState ==='complete'){
    //Para limpiar
    clearInterval(profileScripts)
    //esto es una coll back  para ver como estan los datos en la BD en tiempo real
    //solo al momento de cargar es decir solo una vez
    dbRef.once('value', data=>{
      //once extraela foto por la primera vez
      //valor de la propiedad de los datos key
      //Ver los valores val()
      //c(data, data.key, data.val())
      //Por cada dato que traiga la bd
      /*comparar  va a ser comparado con el uid de el usuario con el que
      se loogeo*/
      data.forEach(photo =>{
      if(photo.val().uid===user.uid){
        //concateno una img
        profilePhotos += `<img src="${photo.val().photoURL}">`
      }
      })
      /*Que en el selector de profile photo guarde el valor
       de dicha foto profilePhotos*/
      d.querySelector(".Profile-photos").innerHTML=profilePhotos
    })
    //Cada vez que tome una nueva foto lo detecte y lo envie al timeline del profile
    dbRef.on('child_added', data=>{
      /*va agregando al elemento de las fotos las imagenes
      deacuerdo a como se va tomando*/
      if(data.val().uid===user.uid){
        //Cuando se detecte un contenido nuevo se agrege la imagen que se esta obteniendoo
        d.querySelector(".Profile-photos").innerHTML += `<img src="${data.val().photoURL}">`
      }
      })

  }
},100)
  return  `
  <article class="Profile  Content-section  u-hide">
  <h2 class="Profile-name">${user.displayName}</h2>
  <p class="Profile-email">${user.email}</p>
  <img class="Profile-avatar" src="${user.photoURL}"></img>
  <h3 class='u-title'>Tus Fotos</h3>
  <aside class="Profile-photos"></aside>

  </article>
  `
}

export default profile
//<p>${user.uid}</p> con este muestras el id de la foto



