import firebase from 'firebase'
import app from './app'
import { pwa, isOnLine } from './helpers/init';


const d = document,
  c = console.log

//Codigo para logguearme
const githubSignIn = () => {
  const provider = new firebase.auth.GithubAuthProvider()
  firebase.auth().signInWithPopup(provider)
    .then(result => c(`${result.user.email} ha iniciado sesin con GitHub`, result))
    .catch(err => c(`Error: ${err.code}: ${err.message}`))
}

//Codigo para desconectarme de github
const githubSignOut = () => {
  firebase.auth().signOut()
  .then( () => c('Te has desconectado correctamente de GitHub') )
  .catch( () => c('Ocurri un error al desconectarse de GitHub') )

}

//Permite conectarse
const signIn = () => {
  //busca un selector valido
  d.addEventListener('click', e => {
    if (e.target.matches('.Sign-button'))
      //alert("Solicitando permiso github")
      githubSignIn()
  })
  //retorna un codigo html y se activa la clase app
  return `
    <div class="Sign">
      <h1 class="Sign-title">EDgram</h1>
      <button class="Sign-button">
        <i class="fa fa-sign-in"></i>
        Entra con
        <i class="fa fa-github"></i>
      </button>
    </div>
  `
}

//Permite desconectarme
export const signOut = () => {
  d.addEventListener('click', e => {
    if ( e.target.matches('.logout') )
      githubSignOut()
  })

  return `
    <button class="logout" title="Salir">
      <i class="logout fa fa-sign-out"></i>
    </button>
  `
}

/*va a revisar el estado de la conexion
Evalua si el usuario esta activo y tiene credenciales validas*/
export const isAuth = () => {
  firebase.auth().onAuthStateChanged(user => {
    const EDgram = d.querySelector('.EDgram')
    // c(user)

    if ( user ) {
      EDgram.innerHTML = app()
      EDgram.classList.add('u-jc-flex-start')
      pwa()
      //c('Usuario Autenticado')
    } else {
      EDgram.innerHTML = signIn()
      EDgram.classList.remove('u-jc-flex-start')
      //c('Usuario NO Autenticado')
    }

    isOnLine()
  })
}



